.mimic-col.side-left,
.mimic-col.side-right {
    /* SCROLL UNIVERSAL: No sticky */
}

.mimic-wrap {
    background: transparent;
    border: none;
    border-radius: 20px;
    padding: 0;
    box-shadow: none;
    backdrop-filter: none;
    -webkit-backdrop-filter: none;
}

.mimic-wrap-inner {
    background: transparent;
    border: none;
    border-radius: 18px;
    padding: 0;
    box-shadow: none;
}

.subgrid.two {
