}

.panel-grid.two-columns {
    grid-template-columns: 1.25fr 1fr;
}

.panel-grid.stacked {
    align-items: start;
}

.dashboard-section .dashboard-top-header h1 {
    font-size: var(--dashboard-text-2xl);
    font-weight: var(--dashboard-font-bold);
    color: var(--text-primary);
    margin: 0 0 4px 0;
    color: var(--theme-text-secondary);
}

.dashboard-section .text-muted {
    color: var(--text-secondary);
    font-size: var(--dashboard-text-base);
    margin: 0;
}

.dashboard-section .btn-refresh {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 20px;
    background: white;
    border: 1px solid var(--border-color);
    border-radius: 10px;
    color: var(--text-primary);
    font-size: var(--dashboard-text-base);
    font-weight: var(--dashboard-font-medium);
    cursor: pointer;
    transition: all 0.2s ease;
    box-shadow: var(--shadow-sm);
}

.dashboard-section .btn-refresh:hover {
    border-color: #667eea;
    color: #667eea;
    box-shadow: var(--shadow-md);
    transform: translateY(-2px);
}

.dashboard-section .btn-refresh svg {
    transition: transform 0.4s ease;
}

.dashboard-section .btn-refresh:hover svg {
    transform: rotate(180deg);
}

/* Stats Grid with Color Pops */
.dashboard-section .stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 14px;
    margin-bottom: 20px;
}

.dashboard-section .stats-grid .stat-card.stat-summary {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 14px;
    align-items: start;
    padding: 18px;
}

.monitor-card {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: minmax(280px, 1fr) minmax(320px, 1.1fr);
    gap: 16px;
    align-items: start;
    padding: 16px;
}

.monitor-wrapper {
    grid-column: 1 / -1;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.monitor-header-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 12px;
    padding: 0 4px;
}

.monitor-title {
    font-family: 'Cinzel', serif;
    font-size: 22px;
    font-weight: var(--dashboard-font-bold);
    color: var(--dashboard-text-primary);
    margin: 0;
    letter-spacing: 0.3px;
}

.expand-all-btn {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    padding: 8px 14px;
    background: rgba(139, 35, 72, 0.08);
    border: 1px solid rgba(139, 35, 72, 0.18);
    border-radius: 12px;
    color: var(--theme-accent-color);
    font-size: 13px;
    font-weight: var(--dashboard-font-semibold);
    cursor: pointer;
    transition: all 0.2s ease;
}

.expand-all-btn:hover {
    background: rgba(139, 35, 72, 0.14);
    border-color: var(--theme-accent-color);
    transform: translateY(-1px);
}

.expand-all-btn svg {
    transition: transform 0.2s ease;
}

.expand-all-btn[data-expanded="true"] svg {
    transform: rotate(180deg);
}

.pending-count {
    display: inline-block;
    padding: 2px 8px;
    background: var(--theme-accent-color);
    color: white;
    border-radius: 8px;
    font-size: 11px;
    font-weight: var(--dashboard-font-bold);
    margin-left: 2px;
}

.monitor-visual {
    display: grid;
    grid-template-columns: 120px 1fr;
    gap: 12px;
    align-items: center;
    padding: 12px;
    border-right: 1px solid var(--dashboard-border);
}

.monitor-actions {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 12px;
}

.summary-block {
    padding: 10px;
    border-left: 2px solid var(--dashboard-border-subtle);
}

.summary-head-inline {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    margin-bottom: 8px;
}

.summary-title {
    color: var(--dashboard-card-heading);
    font-weight: var(--dashboard-font-bold);
    font-size: var(--dashboard-text-base);
}

.summary-list.compact li {
    padding: 9px 10px;
    color: var(--dashboard-card-text);
}

.donut-heading {
    color: var(--dashboard-card-heading);
    font-weight: 700;
}

.donut-desc {
    color: var(--dashboard-card-meta);
}

.link-btn.subtle {
    margin-top: 6px;
    font-weight: var(--dashboard-font-semibold);
    color: var(--theme-text-secondary);
}

.link-btn.subtle:hover {
    color: var(--theme-accent-color, #3b82f6);
    background: rgba(59, 130, 246, 0.08);
}

.stat-summary .summary-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 12px;
}

.stat-summary .summary-head h3 {
    margin: 2px 0 0;
    font-size: var(--dashboard-text-lg);
}

.stat-summary .summary-columns {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 14px;
}

.stat-summary .summary-title {
    margin: 0 0 6px;
    font-weight: var(--dashboard-font-bold);
    color: var(--theme-text-primary);
}

.stat-summary .summary-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    gap: 8px;
}

.stat-summary .summary-list li {
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
    gap: 8px;
    background: rgba(255, 255, 255, 0.6);
    border: 1px solid rgba(15, 23, 42, 0.06);
    border-radius: 12px;
    padding: 10px 12px;
    color: var(--text-primary);
}

.stat-summary .summary-list .dot {
    width: 9px;
    height: 9px;
}

.stat-summary .summary-list .dot.info {
    background: var(--theme-accent-color, #3b82f6);
    box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.1);
}

.stat-summary .summary-list .dot.warn {
    background: #f59e0b;
    box-shadow: 0 0 0 4px rgba(245, 158, 11, 0.14);
}

.stat-summary .summary-list .dot.ok {
    background: #38ef7d;
    box-shadow: 0 0 0 4px rgba(56, 239, 125, 0.14);
}

.stat-summary .link-btn {
    border: none;
    background: transparent;
    color: var(--theme-accent-color, #3b82f6);
    font-weight: 700;
    cursor: pointer;
    padding: 4px 8px;
    border-radius: 10px;
    transition: background 0.2s ease, color 0.2s ease;
}

.stat-summary .link-btn:hover {
    background: rgba(59, 130, 246, 0.1);
}

.dashboard-section .stats-grid.compact {
    gap: 16px;
    margin-bottom: 12px;
}

.dashboard-section .stat-card {
    background: linear-gradient(145deg, rgba(255, 255, 255, 0.96) 0%, rgba(248, 250, 252, 0.92) 100%);
    border-radius: 16px;
    padding: 14px;
    display: flex;
    align-items: center;
    gap: 12px;
    position: relative;
    overflow: hidden;
    box-shadow: 0 20px 50px rgba(15, 23, 42, 0.12), 0 8px 24px rgba(139, 35, 72, 0.08), inset 0 1px 0 rgba(255, 255, 255, 0.5);
    border: 1px solid rgba(226, 232, 240, 0.6);
    backdrop-filter: blur(28px) saturate(1.5);
    -webkit-backdrop-filter: blur(28px) saturate(1.5);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.dashboard-section .stat-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 4px;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.dashboard-section .stat-card.stat-primary::before {
    background: var(--color-primary);
}

.dashboard-section .stat-card.stat-success::before {
    background: var(--color-success);
}

.dashboard-section .stat-card.stat-warning::before {
    background: var(--color-warning);
}

.dashboard-section .stat-card.stat-info::before {
    background: var(--color-info);
}

.dashboard-section .stat-card:hover {
    transform: translateY(-4px);
    box-shadow: var(--shadow-lg);
}

.dashboard-section .stat-card:hover::before {
    opacity: 1;
}

.dashboard-section .stat-card.stat-graph {
    align-items: center;
    gap: 16px;
}

.stat-donut-card {
    min-height: 140px;
}

.donut-figure {
    width: 120px;
    height: 120px;
    position: relative;
    flex-shrink: 0;
}

.donut-svg {
    width: 100%;
    height: 100%;
}

.donut-track {
    fill: none;
    stroke: rgba(15, 23, 42, 0.12);
    stroke-width: 12;
}

.donut-segment {
    fill: none;
    stroke-width: 12;
    stroke-linecap: round;
    transform: rotate(-90deg);
    transform-origin: center;
}

.donut-segment.active {
    stroke: #16a34a;
}

.donut-segment.renew {
    stroke: #f59e0b;
}

.donut-segment.risk {
    stroke: #e11d48;
}

.donut-title {
    font-size: var(--dashboard-text-lg);
    font-weight: var(--dashboard-font-extrabold);
    fill: var(--theme-text-primary);
}

.donut-sub {
    font-size: var(--dashboard-text-sm);
    fill: var(--theme-text-secondary);
    letter-spacing: 0.04em;
}

.donut-meta {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 6px;
}

.donut-heading {
    font-weight: var(--dashboard-font-bold);
    color: var(--theme-text-primary);
}

.donut-desc {
    margin: 0;
    color: var(--theme-text-secondary);
    font-size: var(--dashboard-text-base);
}

.donut-legend {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.donut-key {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    color: var(--theme-text-secondary);
    font-weight: 600;
}

.donut-key .dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.04);
}

.donut-key .dot.active {
    background: #38ef7d;
    box-shadow: 0 0 0 4px rgba(56, 239, 125, 0.18);
}

.donut-key .dot.renew {
    background: #f8c547;
    box-shadow: 0 0 0 4px rgba(248, 197, 71, 0.18);
}

.donut-key .dot.risk {
    background: #f5576c;
    box-shadow: 0 0 0 4px rgba(245, 87, 108, 0.18);
}

.dashboard-section .stat-icon-wrapper {
    width: 60px;
    height: 60px;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

.dashboard-section .stat-primary .stat-icon-wrapper {
    background: var(--color-primary);
}

.dashboard-section .stat-success .stat-icon-wrapper {
    background: var(--color-success);
}

.dashboard-section .stat-warning .stat-icon-wrapper {
    background: var(--color-warning);
}

.dashboard-section .stat-info .stat-icon-wrapper {
    background: var(--color-info);
}

.dashboard-section .stat-icon-wrapper svg {
    stroke: white;
}

.dashboard-section .stat-content {
    flex: 1;
}

.dashboard-section .stat-value {
    font-size: var(--dashboard-text-2xl);
    font-weight: var(--dashboard-font-bold);
    color: var(--dashboard-stat-value);
    margin: 0 0 4px 0;
    line-height: 1;
}

.dashboard-section .stat-label {
    font-size: var(--dashboard-text-base);
    color: var(--dashboard-stat-label);
    font-weight: var(--dashboard-font-semibold);
}

.dashboard-section .stat-trend,
.dashboard-section .stat-badge {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: var(--dashboard-text-sm);
    font-weight: var(--dashboard-font-semibold);
    padding: 4px 10px;
    border-radius: 20px;
}

.dashboard-section .stat-trend.positive {
    color: #1e8449;
    background: rgba(45, 189, 99, 0.15);
    font-weight: 700;
}

.dashboard-section .stat-trend.negative {
    color: #c0392b;
    background: rgba(192, 57, 43, 0.15);
    font-weight: 700;
}

.dashboard-section .stat-badge.success {
    color: #1e8449;
    background: rgba(45, 189, 99, 0.15);
    border: 1px solid rgba(45, 189, 99, 0.3);
    font-weight: 700;
}

.dashboard-section .stat-badge.warning {
    color: #fee140;
    background: rgba(254, 225, 64, 0.15);
}

/* Content Grid */
.dashboard-section .content-grid {
    display: grid;
    grid-template-columns: 1fr 360px;
    gap: 16px;
}

.client-dashboard .content-grid,
.agent-dashboard .content-grid {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 14px;
}

.dashboard-section .content-main {
    display: flex;
    flex-direction: column;
    gap: 14px;
}

/* Content Cards */
.dashboard-section .content-card {
    background: linear-gradient(160deg, rgba(255, 255, 255, 0.96) 0%, rgba(248, 250, 252, 0.92) 100%);
    border-radius: 16px;
    padding: 18px 18px 16px;
    box-shadow: 0 20px 50px rgba(15, 23, 42, 0.12), 0 8px 24px rgba(139, 35, 72, 0.08), inset 0 1px 0 rgba(255, 255, 255, 0.5);
    border: 1px solid rgba(226, 232, 240, 0.6);
    backdrop-filter: blur(28px) saturate(1.4);
    -webkit-backdrop-filter: blur(28px) saturate(1.4);
}

.dashboard-section .content-card.compact,
.dashboard-section .widget-card.compact {
    padding: 18px 18px 16px;
    border-radius: 14px;
}

.card-body-scroll {
    max-height: 340px;
    overflow: auto;
    padding-right: 4px;
    scrollbar-width: thin;
}

.card-body-scroll::-webkit-scrollbar {
    width: 8px;
}

.card-body-scroll::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.dashboard-section .card-header-modern {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.dashboard-section .card-header-modern h3 {
    font-size: 18px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0;
}

.dashboard-section .card-badge {
    padding: 6px 12px;
    background: var(--color-primary);
    color: white;
    border-radius: 20px;
    font-size: 12px;
    font-weight: 600;
}

.dashboard-section .btn-icon-sm {
    width: 32px;
    height: 32px;
    border-radius: 8px;
    border: 1px solid var(--border-color);
    background: white;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s ease;
}

.dashboard-section .btn-icon-sm:hover {
    border-color: #667eea;
    background: #f7fafc;
}

/* Sidebar Widgets */
.dashboard-section .content-sidebar {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.dashboard-section .widget-card {
    background: linear-gradient(160deg, rgba(255, 255, 255, 0.86), rgba(255, 255, 255, 0.74));
    border-radius: 16px;
    padding: 20px;
    box-shadow: 0 14px 34px rgba(31, 38, 135, 0.12), var(--shadow-md);
    border: 1px solid rgba(255, 255, 255, 0.55);
    backdrop-filter: blur(14px) saturate(1.1);
    -webkit-backdrop-filter: blur(14px) saturate(1.1);
}

.dashboard-section .widget-card h4 {
    font-size: 16px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 16px 0;
}

/* Chart & data visuals */
.chart-card {
    position: relative;
    padding: 16px;
    border-radius: 14px;
    background: linear-gradient(160deg, rgba(255, 255, 255, 0.96) 0%, rgba(248, 250, 252, 0.92) 100%);
    border: 1px solid rgba(226, 232, 240, 0.6);
    box-shadow: 0 20px 50px rgba(15, 23, 42, 0.12), 0 8px 24px rgba(139, 35, 72, 0.08), inset 0 1px 0 rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(28px) saturate(1.4);
    -webkit-backdrop-filter: blur(28px) saturate(1.4);
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.chart-card:hover {
    transform: translateY(-2px);
    box-shadow: 0 24px 60px rgba(15, 23, 42, 0.16), 0 12px 32px rgba(139, 35, 72, 0.12), inset 0 1px 0 rgba(255, 255, 255, 0.5);
    border-color: var(--theme-accent-color);
}

.calendar-card .calendar-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 8px;
    font-size: 13px;
}

.calendar-card .cal-day {
    padding: 10px;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.12);
    text-align: center;
    font-weight: 700;
    color: var(--theme-text-primary);
    border: 1px solid rgba(255, 255, 255, 0.12);
}

.calendar-card .cal-day.muted {
    opacity: 0.72;
}

.calendar-card .cal-slot {
    grid-column: span 5;
    padding: 10px 12px;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.12);
    color: var(--theme-text-primary);
}

.calendar-card .cal-slot.accent {
    background: rgba(118, 75, 162, 0.12);
    border-color: rgba(118, 75, 162, 0.24);
}

.chart-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.chart-title {
    font-size: 15px;
    font-weight: 700;
    color: var(--theme-text-primary);
    margin: 0;
    display: inline-flex;
    align-items: center;
    gap: 6px;
}

.chart-legend {
    display: flex;
    align-items: center;
    gap: 10px;
}

.chart-legend-item {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    font-size: 14px;
    color: var(--theme-text-secondary);
}

.chart-legend-dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: var(--theme-accent-color);
    box-shadow: 0 0 0 4px rgba(155, 89, 182, 0.12);
}

.chart-body {
    position: relative;
    margin-top: 6px;
    min-height: 140px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.28));
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    overflow: hidden;
}

.sparkline-placeholder {
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, rgba(139, 35, 72, 0.14), rgba(155, 89, 182, 0.12));
    opacity: 0.55;
}

.chart-metrics {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
    gap: 10px;
    margin-top: 12px;
}

.chart-metric {
    background: rgba(255, 255, 255, 0.82);
    border: 1px solid rgba(255, 255, 255, 0.22);
    border-radius: 10px;
    padding: 10px 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    box-shadow: 0 8px 22px rgba(139, 35, 72, 0.10);
}

.chart-metric .label {
    font-size: 14px;
    color: var(--theme-text-secondary);
}

.chart-metric .value {
    font-size: 16px;
    font-weight: 700;
    color: var(--theme-text-primary);
}

.list-card .mini-table {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.mini-table-row {
    display: grid;
    grid-template-columns: 1.2fr 0.9fr 0.7fr 0.7fr 0.9fr;
    align-items: center;
    gap: 10px;
    padding: 12px 10px;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.76);
    border: 1px solid rgba(0, 0, 0, 0.04);
    box-shadow: 0 10px 26px rgba(0, 0, 0, 0.06);
}

.mini-table-head {
    background: rgba(255, 255, 255, 0.54);
    border: 1px solid rgba(0, 0, 0, 0.04);
    font-weight: 700;
    color: var(--theme-text-secondary);
    text-transform: uppercase;
    font-size: 12px;
    letter-spacing: 0.08em;
}

.mini-table-row span {
    color: var(--theme-text-primary);
    font-size: 14px;
}

.mini-table-row .cell-strong {
    font-weight: 700;
}

.chip {
    display: inline-flex;
    align-items: center;
    padding: 4px 10px;
    border-radius: 999px;
    font-weight: 700;
    font-size: 12px;
    border: 1px solid transparent;
}

.chip-low {
    background: rgba(56, 239, 125, 0.14);
    color: #1c7d4f;
    border-color: rgba(56, 239, 125, 0.28);
}

.chip-medium {
    background: rgba(248, 197, 71, 0.16);
    color: #8a5b00;
    border-color: rgba(248, 197, 71, 0.28);
}

.chip-high {
    background: rgba(245, 87, 108, 0.16);
    color: #982b3a;
    border-color: rgba(245, 87, 108, 0.3);
}

.contact-chips {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.contact-chip {
    border: 1px solid rgba(0, 0, 0, 0.06);
    background: linear-gradient(145deg, rgba(118, 75, 162, 0.08), rgba(102, 126, 234, 0.08));
    color: var(--theme-text-primary);
    padding: 8px 12px;
    border-radius: 999px;
    font-weight: 700;
    font-size: 13px;
    cursor: pointer;
    transition: all 0.2s ease;
    box-shadow: 0 10px 26px rgba(0, 0, 0, 0.06);
}

.contact-chip:hover,
.contact-chip:focus-visible {
    outline: none;
    border-color: var(--theme-accent-color);
    box-shadow: 0 12px 30px rgba(118, 75, 162, 0.16);
    transform: translateY(-1px);
}

.contact-chip:active {
    transform: translateY(0);
}

.status-dot {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    font-weight: 600;
}

.status-dot::before {
    content: '';
    width: 10px;
    height: 10px;
    border-radius: 50%;
    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.06);
}

.status-dot.ok::before {
    background: #38ef7d;
    box-shadow: 0 0 0 4px rgba(56, 239, 125, 0.14);
}

.status-dot.warn::before {
    background: #f8c547;
    box-shadow: 0 0 0 4px rgba(248, 197, 71, 0.18);
}

.status-dot.risk::before {
    background: #f5576c;
    box-shadow: 0 0 0 4px rgba(245, 87, 108, 0.16);
}

.icon-animated {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    margin-right: 4px;
    color: var(--theme-primary-color);
}

.icon-animated svg {
    stroke-linecap: round;
    stroke-linejoin: round;
    overflow: visible;
}

.icon-animated svg * {
    stroke-dasharray: 120;
    stroke-dashoffset: 120;
    animation: line-draw 1.2s ease forwards;
    animation-delay: 0.05s;
}

@keyframes line-draw {
    to {
        stroke-dashoffset: 0;
    }
}

.dashboard-card {
    background: linear-gradient(160deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.78));
    border: 1px solid rgba(255, 255, 255, 0.6);
    border-radius: 16px;
    padding: var(--dashboard-card-padding);
    box-shadow: 0 14px 34px rgba(31, 38, 135, 0.12), var(--shadow-sm);
    backdrop-filter: blur(12px) saturate(1.08);
    -webkit-backdrop-filter: blur(12px) saturate(1.08);
    margin-bottom: 12px;
    color: var(--theme-text-primary);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.2s ease;
}

.dashboard-card.scrolled-collapsed {
    transform: scale(0.98);
    opacity: 0.85;
}

