/* Responsive Design */
@media (max-width: 1200px) {
    .dashboard-section .dashboard-layout {
        grid-template-columns: var(--sidebar-width) 1fr;
    }

    .dashboard-right-rail {
        display: none;
    }

    .dashboard-section .content-grid {
        grid-template-columns: 1fr;
    }

    .dashboard-section .content-sidebar {
        grid-column: 1;
    }

    .panel-grid.two-columns {
        grid-template-columns: 1fr;
    }

    .dashboard-stage {
        padding: 16px;
    }
}

@media (max-width: 768px) {
    .dashboard-section .dashboard-layout {
        grid-template-columns: var(--sidebar-slim-width) 1fr;
        gap: 4px;
        padding: 4px;
    }

    .dashboard-section .dashboard-sidebar {
        width: var(--sidebar-slim-width);
    }

    .dashboard-section .nav-item span,
    .dashboard-section .sidebar-header h3,
    .sidebar-branding .brand-text,
    .sidebar-quick-actions,
    .sidebar-widget {
        display: none;
    }

    .sidebar-surface {
        padding: 8px 4px;
    }

    .dashboard-section .stats-grid {
        grid-template-columns: 1fr;
        gap: 8px;
    }

    .stat-card {
        padding: 12px;
    }

    .dashboard-section .dashboard-top-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 12px;
    }

    .hero-surface {
        padding: 12px;
    }

    .hero-surface .hero-grid {
        grid-template-columns: 1fr;
        gap: 12px;
    }

    .search-bar {
        width: 100%;
    }

    .dashboard-tabs {
        width: 100%;
        justify-content: space-between;
        gap: 4px;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
    }

    .dashboard-tab {
        flex: 1 1 auto;
        min-width: 80px;
        text-align: center;
        font-size: 0.8rem;
        padding: 8px 12px;
        white-space: nowrap;
    }

    .dashboard-stage {
        padding: 8px 4px;
    }

    .content-grid {
        grid-template-columns: 1fr !important;
        gap: 12px;
    }

    .dashboard-card {
        margin-bottom: 8px;
    }

    .monitor-wrapper .monitor-card {
        grid-template-columns: 1fr;
        gap: 12px;
    }

    .monitor-visual {
        grid-template-columns: 100px 1fr;
        border-right: none;
        border-bottom: 1px solid var(--dashboard-border);
        padding-bottom: 12px;
    }
}

/* Mobile phones - stack everything */
@media (max-width: 480px) {
    .dashboard-section .dashboard-layout {
        grid-template-columns: 1fr;
        gap: 0;
        padding: 0;
    }

    .dashboard-section .dashboard-sidebar {
        display: none;
    }

    .dashboard-section .dashboard-right-rail {
        display: none;
    }

    .dashboard-main-wrapper {
        width: 100%;
        padding: 0;
    }

    .dashboard-stage {
        padding: 8px;
    }

    .hero-surface {
        padding: 12px;
        border-radius: 12px;
    }

    .hero-welcome {
        font-size: 24px;
    }

    .hero-title-row .eyebrow {
        font-size: 16px;
    }

    .hero-description {
        font-size: 14px;
    }

    .dashboard-tabs {
        padding: 8px 4px;
        gap: 4px;
    }

    .dashboard-tab {
        font-size: 0.75rem;
        padding: 6px 8px;
        min-width: 70px;
    }

    .stats-grid {
        grid-template-columns: 1fr;
        gap: 8px;
    }

    .stat-card {
        padding: 10px;
    }

    .stat-value {
        font-size: 1.5rem;
    }

    .stat-label {
        font-size: 0.8rem;
    }

    .monitor-header-row {
        flex-direction: column;
        align-items: flex-start;
        gap: 8px;
    }

    .monitor-title {
        font-size: 18px;
    }

    .expand-all-btn {
        width: 100%;
        justify-content: center;
    }

    .monitor-card {
        grid-template-columns: 1fr;
        padding: 12px;
    }

    .monitor-visual {
        grid-template-columns: 1fr;
        text-align: center;
        border-right: none;
        border-bottom: 1px solid var(--dashboard-border);
        padding-bottom: 12px;
        margin-bottom: 12px;
    }

    .donut-figure {
        margin: 0 auto 12px;
    }

    .monitor-actions {
        grid-template-columns: 1fr;
    }

    .dashboard-card {
        padding: 12px;
        margin-bottom: 8px;
    }

    .card-header-modern {
        flex-direction: column;
        align-items: flex-start;
        gap: 8px;
