,mBAAmB;IACnB,aAAa;IACb,qCAAqC;AACzC;;AAEA;IACI,mBAAmB;AACvB;;AAEA,+CAA+C;;AAE/C,sCAAsC;AACtC;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,yBAAyB;AAC7B;;AAEA;IACI,2BAA2B;IAC3B,0CAA0C;AAC9C;;AAEA,yBAAyB;AACzB;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,qCAAqC;IACrC,2BAA2B;IAC3B,mBAAmB;IACnB,aAAa;IACb,qCAAqC;AACzC;;AAEA;;wEAEwE;;AAExE,8BAA8B","sourcesContent":["/* Shared Dashboard UI (restored from backup, tokenized)\n   This file provides the missing layout/components for dashboards:\n   - grid layout\n   - cards + headers\n   - lists (policies/quotes)\n   - tables\n   - badges\n   - icon buttons\n   - sidebar widgets\n*/\n\n/* Import Font Manager for centralized typography & colors */\n@import './Font-Manager.css';\n\n:root {\n    /* Modern Dashboard Colors - Aligned with Brand Palette */\n    --sidebar-width: 300px;\n    --sidebar-slim-width: 80px;\n    --rail-width: 300px;\n    --dashboard-max-width: 1680px;\n    --dashboard-content-width: 90%;\n\n    /* Brand gradients */\n    --color-primary: linear-gradient(135deg, #8b2348 0%, #9b59b6 100%);\n    --color-success: linear-gradient(135deg, #28a745 0%, #20c997 100%);\n    --color-warning: linear-gradient(135deg, #ffc107 0%, #fd7e14 100%);\n    --color-info: linear-gradient(135deg, #17a2b8 0%, #20c997 100%);\n\n    /* Background & Surface Colors */\n    --bg-main: #f8f9fa;\n    --bg-card: #ffffff;\n    --bg-sidebar: #0d1117;\n    --bg-dashboard: linear-gradient(135deg, #f6f7fb 0%, #eef1f7 45%, #e7ebf3 100%);\n\n    /* Legacy compatibility */\n    --dashboard-bg-light: var(--bg-main);\n    --dashboard-stat-gap-shared: 14px;\n    --dashboard-stat-icon-size-shared: 60px;\n    --dashboard-stat-icon-radius-shared: 14px;\n    --dashboard-stat-transition-shared: all 0.42s cubic-bezier(0.33, 1, 0.68, 1);\n\n    /* Theme-adaptive variables for high contrast */\n    --dashboard-bg-surface: rgba(255, 255, 255, 0.92);\n    --dashboard-border: rgba(0, 0, 0, 0.06);\n    --dashboard-border-subtle: rgba(0, 0, 0, 0.08);\n    --dashboard-radius-lg: 18px;\n}\n\n/* Dark theme overrides */\nhtml[data-theme=\"dark-forest\"] {\n    --dashboard-bg-surface: rgba(20, 30, 25, 0.88);\n    --dashboard-border: rgba(255, 255, 255, 0.1);\n    --dashboard-border-subtle: rgba(255, 255, 255, 0.12);\n}\n\n/* Dashboard shell & layout */\n.dashboard-section {\n    background: var(--bg-dashboard);\n    height: 100vh;\n    width: 100vw;\n    margin: 0;\n    padding: 0;\n    /* SCROLL UNIVERSAL: overflow-y manejado por scroll-modal-fixes.css */\n    display: flex;\n    flex-direction: column;\n}\n\nhtml[data-theme=\"dark-forest\"] .dashboard-section {\n    background: #060b0a;\n}\n\n/* Hero Container - Full Width Outside Grid */\n.dashboard-section .dashboard-hero-container {\n    width: 100%;\n    flex-shrink: 0;\n    padding: 12px 12px 12px 12px;\n}\n\n.dashboard-section .dashboard-layout {\n    display: grid;\n    grid-template-columns: var(--sidebar-width) minmax(0, 1fr) var(--rail-width);\n    gap: 0;\n    flex: 1;\n    min-height: 0;\n    width: 100%;\n    max-width: 100vw;\n    margin: 0;\n    padding: 0;\n    /* SCROLL UNIVERSAL: overflow manejado por scroll-modal-fixes.css */\n}\n\n.dashboard-section .dashboard-main-wrapper {\n    /* SCROLL UNIVERSAL: height y overflow manejados por scroll-modal-fixes.css */\n    min-width: 0;\n    width: 100%;\n}\n\n.dashboard-shell {\n    width: 100%;\n    margin: 0;\n    padding: 0;\n}\n\n.dashboard-section.no-sidebar .dashboard-layout {\n    display: block;\n}\n\n.dashboard-section.no-sidebar .dashboard-main-wrapper {\n    margin-left: 0;\n    padding: 0;\n    max-width: none;\n}\n\n/* Floating right rail */\n.dashboard-section.no-sidebar .dashboard-shell {\n    padding: 0 20px 36px;\n}\n\n/* ===== DASHBOARD HEADER - SHARED ACROSS ALL DASHBOARDS ===== */\n.dashboard-section .dashboard-header {\n    display: none !important;\n}\n\n/* Ocultar navbar global cuando se navega a dashboards (evita header doble) */\nbody[data-page=\"client-dashboard\"] .navbar,\nbody[data-page=\"agent-dashboard\"] .navbar,\nbody[data-page=\"admin-dashboard\"] .navbar,\nbody[data-page=\"client-dashboard\"] .navbar-header,\nbody[data-page=\"agent-dashboard\"] .navbar-header,\nbody[data-page=\"admin-dashboard\"] .navbar-header {\n    display: none !important;\n}\n\n.dashboard-section .dashboard-header>div:first-child h1 {\n    font-family: var(--dashboard-font-family-primary);\n    font-size: var(--dashboard-text-2xl);\n    color: var(--theme-accent-color);\n    margin: 0 0 8px 0;\n    font-weight: var(--dashboard-font-bold);\n    text-shadow: 0 2px 8px var(--theme-primary-shadow-color);\n}\n\n.dashboard-section .dashboard-header>div:first-child p {\n    color: var(--theme-text-secondary);\n    font-size: var(--dashboard-text-md);\n    margin: 0;\n}\n\n.dashboard-section .dashboard-header .btn {\n    padding: 12px 26px;\n    background: linear-gradient(140deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02));\n    color: var(--theme-text-primary);\n    border: 1px solid var(--theme-accent-border);\n    border-radius: 12px;\n    cursor: pointer;\n    font-weight: var(--dashboard-font-semibold);\n    backdrop-filter: blur(var(--acrylic-blur)) saturate(var(--acrylic-saturate-strong));\n    -webkit-backdrop-filter: blur(var(--acrylic-blur)) saturate(var(--acrylic-saturate-strong));\n    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.22), inset 0 1px 0 rgba(255, 255, 255, 0.14);\n    transition: all 0.3s ease;\n}\n\n.dashboard-section .dashboard-header .btn:hover {\n    transform: translateY(-3px);\n    background: linear-gradient(150deg, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.04));\n    box-shadow: 0 16px 38px rgba(0, 0, 0, 0.3), 0 0 0 1px var(--theme-accent-border);\n}\n\n/* ===== END DASHBOARD HEADER ===== */\n\n/* Integrated content without additional rail wrapping */\n.dashboard-stage {\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n    /* SCROLL UNIVERSAL: overflow manejado por scroll-modal-fixes.css */\n    overflow: visible;\n    height: auto;\n    padding: 0 12px 12px 12px;\n}\n\n/* Scroll collapse behavior:\n   When user scrolls down past threshold (80px), hero-surface collapses.\n   Controlled by scrollCollapse.js which adds/removes COLLAPSE_CLASS.\n   Target: .dashboard-section (scroll universal)\n*/\n\n.stage-main {\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n}\n\n.dashboard-body {\n    display: block;\n    margin-top: 8px;\n}\n\n/* Slim Sidebar Navigation */\n.dashboard-section .dashboard-sidebar {\n    /* SCROLL UNIVERSAL: No sticky, no overflow */\n    width: var(--sidebar-width);\n    display: flex;\n    flex-direction: column;\n    padding: 0;\n}\n\n.dashboard-section .dashboard-sidebar .sidebar-surface {\n    /* SCROLL UNIVERSAL: height auto */\n    height: auto;\n}\n\n.sidebar-stack {\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n    padding: 12px;\n}\n\n.sidebar-block .sidebar-header,\n.sidebar-block .sidebar-quick-actions,\n.sidebar-block .sidebar-widget-group,\n.sidebar-block .sidebar-widget {\n    background: transparent;\n    border: 0;\n    box-shadow: none;\n    padding: 0;\n    margin: 0;\n}\n\n.sidebar-block .sidebar-qa-header {\n    margin-bottom: 8px;\n}\n\n.sidebar-block .qa-btn,\n.sidebar-block .client-pill,\n.sidebar-block .widget-mini-item,\n.sidebar-block .sidebar-logout {\n    width: 100%;\n}\n\n.sidebar-surface {\n    background: transparent;\n    border-radius: 20px;\n    border: 0;\n    box-shadow: none;\n    backdrop-filter: none;\n    -webkit-backdrop-filter: none;\n    padding: 0;\n    /* SCROLL UNIVERSAL: overflow manejado por scroll-modal-fixes.css */\n    overflow: visible;\n    height: auto;\n    display: flex;\n    flex-direction: column;\n    gap: 0;\n}\n\nhtml[data-theme=\"dark-forest\"] .sidebar-surface,\nhtml[data-theme=\"dark-forest\"] .rail-surface {\n    background: transparent;\n    border: 0;\n    box-shadow: none;\n    color: var(--theme-text-primary);\n}\n\n.sidebar-block {\n    background: transparent;\n    border: 0;\n    box-shadow: none;\n    padding: 0;\n}\n\nhtml[data-theme=\"dark-forest\"] .sidebar-block {\n    background: transparent;\n    border: 0;\n    box-shadow: none;\n}\n\n.dashboard-section .sidebar-header {\n    padding: 14px 16px;\n    border-bottom: 1px solid rgba(15, 23, 42, 0.08);\n    text-align: center;\n    flex-shrink: 0;\n}\n\n.dashboard-section .user-avatar {\n    width: 60px;\n    height: 60px;\n    margin: 0 auto 12px;\n    border-radius: 50%;\n    overflow: hidden;\n    box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);\n}\n\n.dashboard-section .sidebar-header h3 {\n    color: var(--dashboard-text-primary);\n    font-size: var(--dashboard-text-md);\n    font-weight: var(--dashboard-font-semibold);\n    margin: 0;\n}\n\n.dashboard-section .sidebar-header.sidebar-branding {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 10px;\n    padding: 16px 16px 14px;\n    flex-shrink: 0;\n}\n\n.sidebar-branding .logo-slot-nav {\n    width: 64px;\n    height: 64px;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    color: var(--theme-primary-color);\n}\n\n.sidebar-branding .brand-text-sidebar {\n    text-align: center;\n    display: flex;\n    flex-direction: column;\n    gap: 2px;\n}\n\n.sidebar-branding .brand-text-sidebar .brand-name {\n    color: var(--theme-text-primary);\n    font-size: var(--dashboard-text-md);\n    letter-spacing: 0.04em;\n    font-weight: var(--dashboard-font-bold);\n}\n\n.sidebar-branding .brand-text-sidebar .brand-tagline {\n    color: var(--theme-text-secondary);\n    font-size: var(--dashboard-text-xs);\n    letter-spacing: 0.12em;\n    text-transform: uppercase;\n}\n\n.dashboard-section .sidebar-nav {\n    color: var(--theme-text-primary);\n    padding: 8px 0;\n    display: flex;\n    flex-direction: column;\n    gap: 2px;\n    flex-shrink: 0;\n}\n\n.sidebar-quick-actions,\n.sidebar-widget-group {\n    padding: 12px;\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n    border: 0;\n    background: transparent;\n    border-radius: 14px;\n    box-shadow: none;\n    backdrop-filter: none;\n    -webkit-backdrop-filter: none;\n    flex-shrink: 0;\n}\n\nhtml[data-theme=\"dark-forest\"] .sidebar-quick-actions,\nhtml[data-theme=\"dark-forest\"] .sidebar-widget-group {\n    background: transparent;\n    border: 0;\n    box-shadow: none;\n}\n\n.sidebar-qa-header,\n.sidebar-widget-group .sidebar-qa-header {\n    font-size: var(--dashboard-text-sm);\n    text-transform: uppercase;\n    letter-spacing: 0.08em;\n    color: var(--theme-text-secondary);\n    margin-bottom: 6px;\n    font-weight: var(--dashboard-font-bold);\n}\n\n.qa-btn,\n.client-pill,\n.widget-mini-item {\n    display: flex;\n    align-items: center;\n    gap: 10px;\n    padding: 8px 10px;\n    border-radius: 12px;\n    border: 1px solid rgba(0, 0, 0, 0.10);\n    background: rgba(255, 255, 255, 0.45) !important;\n    color: var(--theme-text-primary);\n    cursor: pointer;\n    transition: all 0.2s ease;\n    font-weight: var(--dashboard-font-semibold);\n    text-align: left;\n    box-shadow: none !important;\n    backdrop-filter: blur(8px);\n    -webkit-backdrop-filter: blur(8px);\n}\n\nhtml[data-theme=\"dark-forest\"] .qa-btn,\nhtml[data-theme=\"dark-forest\"] .client-pill,\nhtml[data-theme=\"dark-forest\"] .widget-mini-item {\n    background: rgba(10, 22, 19, 0.35) !important;\n    border: 1px solid rgba(76, 201, 191, 0.16) !important;\n    color: var(--theme-text-primary);\n    box-shadow: none !important;\n    backdrop-filter: blur(8px);\n    -webkit-backdrop-filter: blur(8px);\n}\n\n/* Ajuste de badges en tema oscuro: gradiente y legibilidad */\nhtml[data-theme=\"dark-forest\"] .card-badge,\nhtml[data-theme=\"dark-forest\"] .badge,\nhtml[data-theme=\"dark-forest\"] .badge.badge-accent,\nhtml[data-theme=\"dark-forest\"] .badge.badge-light,\nhtml[data-theme=\"dark-forest\"] .badge.badge-primary,\nhtml[data-theme=\"dark-forest\"] .badge.badge-warning,\nhtml[data-theme=\"dark-forest\"] .badge.badge-success {\n    background: linear-gradient(135deg, rgba(76, 201, 191, 0.18), rgba(155, 89, 182, 0.28));\n    border: 1px solid rgba(255, 255, 255, 0.08);\n    color: var(--theme-text-primary);\n    box-shadow: 0 8px 18px rgba(0, 0, 0, 0.32);\n}\n\n/* Evitar contenedor extra en sidebar widgets (light & dark) */\n.sidebar-quick-actions,\n.sidebar-widget-group {\n    background: transparent;\n    border: 0;\n    box-shadow: none;\n}\n\n.sidebar-block.sidebar-logout-block {\n    display: flex;\n    padding: 10px 10px 6px;\n}\n\n.sidebar-block.sidebar-brand-block .sidebar-header {\n    align-items: center;\n}\n\n.qa-btn:hover,\n.client-pill:hover,\n.widget-mini-item:hover {\n    background: var(--theme-accent-bg-soft);\n    border-color: var(--theme-accent-border);\n    transform: translateY(-2px);\n}\n\nhtml[data-theme=\"dark-forest\"] .qa-btn:hover,\nhtml[data-theme=\"dark-forest\"] .client-pill:hover,\nhtml[data-theme=\"dark-forest\"] .widget-mini-item:hover {\n    background: rgba(76, 201, 191, 0.12);\n    border-color: rgba(76, 201, 191, 0.32);\n    transform: translateY(-1px);\n}\n\n/* Ajuste especfico para client-pill y widget-mini-item */\n.client-pill,\n.widget-mini-item {\n    gap: 8px;\n}\n\n.dashboard-section .nav-item {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    padding: 10px 16px;\n    color: var(--theme-text-secondary);\n    text-decoration: none;\n    transition: all 0.2s ease;\n    position: relative;\n}\n\n.dashboard-section .nav-item::before {\n    content: '';\n    position: absolute;\n    left: 0;\n    top: 50%;\n    transform: translateY(-50%);\n    width: 3px;\n    height: 0;\n    background: linear-gradient(180deg, #667eea, #764ba2);\n    border-radius: 0 2px 2px 0;\n    transition: height 0.2s ease;\n}\n\n.dashboard-section .nav-item:hover,\n.dashboard-section .nav-item.active {\n    color: var(--theme-text-primary);\n    background: var(--theme-accent-bg-soft);\n}\n\n.dashboard-section .nav-item.active::before {\n    height: 70%;\n}\n\n.dashboard-section .nav-item svg {\n    flex-shrink: 0;\n}\n\n.dashboard-section .sidebar-logout {\n    padding: 12px;\n    background: rgba(245, 87, 108, 0.08);\n    border: 1px solid rgba(245, 87, 108, 0.25);\n    border-radius: 12px;\n    color: #f5576c;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    gap: 8px;\n    cursor: pointer;\n    transition: all 0.2s ease;\n    font-size: var(--dashboard-text-base);\n    font-weight: var(--dashboard-font-medium);\n    flex-shrink: 0;\n    margin-top: auto;\n}\n\n.dashboard-section .sidebar-logout:hover {\n    background: rgba(245, 87, 108, 0.2);\n    border-color: rgba(245, 87, 108, 0.4);\n}\n\n.sidebar-widget {\n    padding: 12px;\n    border-radius: 14px;\n    background: transparent;\n    border: 0;\n    box-shadow: none;\n    backdrop-filter: none;\n    -webkit-backdrop-filter: none;\n    color: var(--theme-text-primary);\n    flex-shrink: 0;\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n}\n\n.sidebar-widget h4 {\n    margin: 0;\n    font-size: var(--dashboard-text-base);\n    letter-spacing: 0.3px;\n    text-transform: uppercase;\n    color: var(--theme-text-secondary);\n    font-weight: var(--dashboard-font-bold);\n}\n\n.sidebar-widget .card-badge {\n    background: var(--theme-accent-bg-soft);\n    color: var(--theme-accent-color);\n    border: 1px solid var(--theme-accent-border);\n}\n\n.sidebar-widget ul {\n    margin: 0;\n    padding-left: 16px;\n    color: var(--theme-text-primary);\n    display: grid;\n    gap: 6px;\n    font-size: var(--dashboard-text-base);\n}\n\n.sidebar-widget .contact-chips {\n    display: grid;\n    gap: 8px;\n}\n\n.sidebar-widget .contact-chip {\n    background: rgba(255, 255, 255, 0.85);\n    color: var(--theme-text-primary);\n    border: 1px solid rgba(0, 0, 0, 0.10);\n    padding: 8px 10px;\n    border-radius: 12px;\n    text-align: left;\n    width: 100%;\n    cursor: pointer;\n    transition: all 0.2s ease;\n}\n\n/* Main Content Wrapper */\n.dashboard-section .dashboard-main-wrapper {\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n    min-width: 0;\n}\n\n.dashboard-section.no-sidebar .dashboard-main-wrapper {\n    margin-left: 0;\n    padding: 0;\n    max-width: none;\n}\n\n/* Top Header */\n.dashboard-section .dashboard-top-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 22px;\n    gap: 14px;\n}\n\n.dashboard-section .mimic-header {\n    align-items: flex-start;\n}\n\n.search-hero {\n    flex-direction: column;\n    align-items: stretch;\n    gap: 12px;\n}\n\n.search-hero .hero-copy {\n    text-align: center;\n    display: flex;\n    flex-direction: column;\n    gap: 6px;\n}\n\n.hero-title-row {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    gap: 12px;\n    font-weight: var(--dashboard-font-extrabold);\n    font-size: 16px;\n    letter-spacing: 0.4px;\n}\n\n.hero-title-row .eyebrow {\n    color: var(--dashboard-hero-eyebrow);\n    font-size: 14px;\n    font-weight: var(--dashboard-font-semibold);\n    text-transform: uppercase;\n    letter-spacing: 1.2px;\n}\n\n.hero-divider {\n    color: var(--dashboard-text-tertiary);\n    opacity: 0.4;\n    font-size: 14px;\n}\n\n.hero-welcome {\n    color: var(--dashboard-hero-title);\n    font-weight: var(--dashboard-font-bold);\n    font-family: 'Cinzel', serif;\n    font-size: 20px;\n    letter-spacing: 0.5px;\n}\n\n.hero-welcome span[data-user-name],\n.hero-welcome span[data-hero-user],\n.hero-welcome span[data-agent-name] {\n    font-weight: var(--dashboard-font-black);\n    color: var(--dashboard-hero-title);\n}\n\n.hero-description {\n    margin: 6px 0 0;\n    color: var(--dashboard-hero-subtitle);\n    max-width: 760px;\n    align-self: center;\n    line-height: 1.5;\n    font-size: 15px;\n    font-weight: var(--dashboard-font-regular);\n}\n\n.search-hero .hero-search {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    width: 100%;\n}\n\n.hero-surface {\n    position: relative;\n    border-radius: 16px;\n    padding: 16px 20px;\n    margin: 0;\n    width: 100%;\n    background: linear-gradient(160deg, rgba(255, 255, 255, 0.96) 0%, rgba(248, 250, 252, 0.92) 100%);\n    border: 1px solid rgba(226, 232, 240, 0.6);\n    box-shadow: 0 4px 12px rgba(15, 23, 42, 0.08), inset 0 1px 0 rgba(255, 255, 255, 0.5);\n    backdrop-filter: blur(28px) saturate(1.4);\n    -webkit-backdrop-filter: blur(28px) saturate(1.4);\n    overflow: hidden;\n    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.3s ease, max-height 0.3s ease;\n    transform-origin: top center;\n}\n\n.hero-surface.scrolled-collapsed {\n    transform: scaleY(0.75) translateY(-12px);\n    opacity: 0.6;\n    max-height: 60px;\n    overflow: hidden;\n}\n\n.hero-surface::before {\n    content: '';\n    position: absolute;\n    inset: -32% -38% auto auto;\n    width: 280px;\n    height: 180px;\n    background:\n        radial-gradient(circle at 22% 42%, rgba(102, 126, 234, 0.26), transparent 58%),\n        radial-gradient(circle at 74% 60%, rgba(118, 75, 162, 0.22), transparent 60%);\n    filter: blur(6px);\n    opacity: 0.8;\n    pointer-events: none;\n}\n\n.hero-surface .hero-grid {\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n}\n\n.hero-surface .hero-copy {\n    text-align: justify;\n    align-items: flex-start;\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n    width: 100%;\n}\n\n.hero-surface .hero-title-row {\n    display: flex;\n    align-items: center;\n    gap: 10px;\n    flex-wrap: wrap;\n    width: 100%;\n}\n\n.hero-surface .hero-welcome {\n    margin-left: 0;\n}\n\n.hero-surface .hero-title-row .hero-description {\n    flex: 1;\n    margin: 0;\n    margin-left: auto;\n    text-align: justify;\n    min-width: 300px;\n}\n\n.hero-surface .hero-search {\n    width: 100%;\n    display: flex;\n    align-items: center;\n    gap: 12px;\n}\n\n.search-hero .search-bar.stretch {\n    flex: 1;\n    width: 100%;\n}\n\n.dashboard-section .mimic-header .top-left h1 {\n    color: var(--theme-text-primary);\n}\n\n.dashboard-section .top-right {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    flex-wrap: wrap;\n    justify-content: flex-end;\n    align-self: flex-start;\n}\n\n.floating-header-bar {\n    display: inline-flex;\n    align-items: center;\n    gap: 10px;\n    padding: 10px 12px;\n    background: rgba(255, 255, 255, 0.85);\n    border: 1px solid rgba(0, 0, 0, 0.06);\n    border-radius: 16px;\n    box-shadow: 0 16px 36px rgba(0, 0, 0, 0.1);\n    backdrop-filter: blur(14px) saturate(1.05);\n    -webkit-backdrop-filter: blur(14px) saturate(1.05);\n}\n\n.pill-actions {\n    display: inline-flex;\n    align-items: center;\n    gap: 8px;\n}\n\n.search-bar {\n    display: inline-flex;\n    align-items: center;\n    gap: 8px;\n    padding: 10px 12px;\n    background: rgba(255, 255, 255, 0.92);\n    border: 1px solid rgba(0, 0, 0, 0.08);\n    border-radius: 14px;\n    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.06);\n    min-width: 320px;\n    backdrop-filter: blur(12px) saturate(1.05);\n}\n\n.search-bar input {\n    border: none;\n    background: transparent;\n    outline: none;\n    width: 100%;\n    font-size: var(--dashboard-text-base);\n    color: var(--theme-text-primary);\n}\n\n.search-bar input::placeholder {\n    color: #64748b;\n}\n\n.header-actions.tight {\n    gap: 10px;\n}\n\n.icon-pill {\n    position: relative;\n    width: 38px;\n    height: 38px;\n    border-radius: 12px;\n    border: 1px solid rgba(0, 0, 0, 0.10);\n    background: rgba(255, 255, 255, 0.94);\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    flex-shrink: 0;\n    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.08);\n    cursor: pointer;\n    transition: transform 0.2s ease, box-shadow 0.2s ease, border-color 0.2s e