-items: center;\n    gap: 16px;\n}\n\n.stat-donut-card {\n    min-height: 140px;\n}\n\n.donut-figure {\n    width: 120px;\n    height: 120px;\n    position: relative;\n    flex-shrink: 0;\n}\n\n.donut-svg {\n    width: 100%;\n    height: 100%;\n}\n\n.donut-track {\n    fill: none;\n    stroke: rgba(15, 23, 42, 0.12);\n    stroke-width: 12;\n}\n\n.donut-segment {\n    fill: none;\n    stroke-width: 12;\n    stroke-linecap: round;\n    transform: rotate(-90deg);\n    transform-origin: center;\n}\n\n.donut-segment.active {\n    stroke: #16a34a;\n}\n\n.donut-segment.renew {\n    stroke: #f59e0b;\n}\n\n.donut-segment.risk {\n    stroke: #e11d48;\n}\n\n.donut-title {\n    font-size: var(--dashboard-text-lg);\n    font-weight: var(--dashboard-font-extrabold);\n    fill: var(--theme-text-primary);\n}\n\n.donut-sub {\n    font-size: var(--dashboard-text-sm);\n    fill: var(--theme-text-secondary);\n    letter-spacing: 0.04em;\n}\n\n.donut-meta {\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n    gap: 6px;\n}\n\n.donut-heading {\n    font-weight: var(--dashboard-font-bold);\n    color: var(--theme-text-primary);\n}\n\n.donut-desc {\n    margin: 0;\n    color: var(--theme-text-secondary);\n    font-size: var(--dashboard-text-base);\n}\n\n.donut-legend {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 10px;\n}\n\n.donut-key {\n    display: inline-flex;\n    align-items: center;\n    gap: 6px;\n    color: var(--theme-text-secondary);\n    font-weight: 600;\n}\n\n.donut-key .dot {\n    width: 10px;\n    height: 10px;\n    border-radius: 50%;\n    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.04);\n}\n\n.donut-key .dot.active {\n    background: #38ef7d;\n    box-shadow: 0 0 0 4px rgba(56, 239, 125, 0.18);\n}\n\n.donut-key .dot.renew {\n    background: #f8c547;\n    box-shadow: 0 0 0 4px rgba(248, 197, 71, 0.18);\n}\n\n.donut-key .dot.risk {\n    background: #f5576c;\n    box-shadow: 0 0 0 4px rgba(245, 87, 108, 0.18);\n}\n\n.dashboard-section .stat-icon-wrapper {\n    width: 60px;\n    height: 60px;\n    border-radius: 12px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-shrink: 0;\n}\n\n.dashboard-section .stat-primary .stat-icon-wrapper {\n    background: var(--color-primary);\n}\n\n.dashboard-section .stat-success .stat-icon-wrapper {\n    background: var(--color-success);\n}\n\n.dashboard-section .stat-warning .stat-icon-wrapper {\n    background: var(--color-warning);\n}\n\n.dashboard-section .stat-info .stat-icon-wrapper {\n    background: var(--color-info);\n}\n\n.dashboard-section .stat-icon-wrapper svg {\n    stroke: white;\n}\n\n.dashboard-section .stat-content {\n    flex: 1;\n}\n\n.dashboard-section .stat-value {\n    font-size: var(--dashboard-text-2xl);\n    font-weight: var(--dashboard-font-bold);\n    color: var(--dashboard-stat-value);\n    margin: 0 0 4px 0;\n    line-height: 1;\n}\n\n.dashboard-section .stat-label {\n    font-size: var(--dashboard-text-base);\n    color: var(--dashboard-stat-label);\n    font-weight: var(--dashboard-font-semibold);\n}\n\n.dashboard-section .stat-trend,\n.dashboard-section .stat-badge {\n    display: flex;\n    align-items: center;\n    gap: 4px;\n    font-size: var(--dashboard-text-sm);\n    font-weight: var(--dashboard-font-semibold);\n    padding: 4px 10px;\n    border-radius: 20px;\n}\n\n.dashboard-section .stat-trend.positive {\n    color: #1e8449;\n    background: rgba(45, 189, 99, 0.15);\n    font-weight: 700;\n}\n\n.dashboard-section .stat-trend.negative {\n    color: #c0392b;\n    background: rgba(192, 57, 43, 0.15);\n    font-weight: 700;\n}\n\n.dashboard-section .stat-badge.success {\n    color: #1e8449;\n    background: rgba(45, 189, 99, 0.15);\n    border: 1px solid rgba(45, 189, 99, 0.3);\n    font-weight: 700;\n}\n\n.dashboard-section .stat-badge.warning {\n    color: #fee140;\n    background: rgba(254, 225, 64, 0.15);\n}\n\n/* Content Grid */\n.dashboard-section .content-grid {\n    display: grid;\n    grid-template-columns: 1fr 360px;\n    gap: 16px;\n}\n\n.client-dashboard .content-grid,\n.agent-dashboard .content-grid {\n    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));\n    gap: 14px;\n}\n\n.dashboard-section .content-main {\n    display: flex;\n    flex-direction: column;\n    gap: 14px;\n}\n\n/* Content Cards */\n.dashboard-section .content-card {\n    background: linear-gradient(160deg, rgba(255, 255, 255, 0.96) 0%, rgba(248, 250, 252, 0.92) 100%);\n    border-radius: 16px;\n    padding: 18px 18px 16px;\n    box-shadow: 0 20px 50px rgba(15, 23, 42, 0.12), 0 8px 24px rgba(139, 35, 72, 0.08), inset 0 1px 0 rgba(255, 255, 255, 0.5);\n    border: 1px solid rgba(226, 232, 240, 0.6);\n    backdrop-filter: blur(28px) saturate(1.4);\n    -webkit-backdrop-filter: blur(28px) saturate(1.4);\n}\n\n.dashboard-section .content-card.compact,\n.dashboard-section .widget-card.compact {\n    padding: 18px 18px 16px;\n    border-radius: 14px;\n}\n\n.card-body-scroll {\n    max-height: 340px;\n    overflow: auto;\n    padding-right: 4px;\n    scrollbar-width: thin;\n}\n\n.card-body-scroll::-webkit-scrollbar {\n    width: 8px;\n}\n\n.card-body-scroll::-webkit-scrollbar-thumb {\n    background: rgba(0, 0, 0, 0.12);\n    border-radius: 10px;\n}\n\n.dashboard-section .card-header-modern {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 20px;\n}\n\n.dashboard-section .card-header-modern h3 {\n    font-size: 18px;\n    font-weight: 600;\n    color: var(--text-primary);\n    margin: 0;\n}\n\n.dashboard-section .card-badge {\n    padding: 6px 12px;\n    background: var(--color-primary);\n    color: white;\n    border-radius: 20px;\n    font-size: 12px;\n    font-weight: 600;\n}\n\n.dashboard-section .btn-icon-sm {\n    width: 32px;\n    height: 32px;\n    border-radius: 8px;\n    border: 1px solid var(--border-color);\n    background: white;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    cursor: pointer;\n    transition: all 0.2s ease;\n}\n\n.dashboard-section .btn-icon-sm:hover {\n    border-color: #667eea;\n    background: #f7fafc;\n}\n\n/* Sidebar Widgets */\n.dashboard-section .content-sidebar {\n    display: flex;\n    flex-direction: column;\n    gap: 20px;\n}\n\n.dashboard-section .widget-card {\n    background: linear-gradient(160deg, rgba(255, 255, 255, 0.86), rgba(255, 255, 255, 0.74));\n    border-radius: 16px;\n    padding: 20px;\n    box-shadow: 0 14px 34px rgba(31, 38, 135, 0.12), var(--shadow-md);\n    border: 1px solid rgba(255, 255, 255, 0.55);\n    backdrop-filter: blur(14px) saturate(1.1);\n    -webkit-backdrop-filter: blur(14px) saturate(1.1);\n}\n\n.dashboard-section .widget-card h4 {\n    font-size: 16px;\n    font-weight: 600;\n    color: var(--text-primary);\n    margin: 0 0 16px 0;\n}\n\n/* Chart & data visuals */\n.chart-card {\n    position: relative;\n    padding: 16px;\n    border-radius: 14px;\n    background: linear-gradient(160deg, rgba(255, 255, 255, 0.96) 0%, rgba(248, 250, 252, 0.92) 100%);\n    border: 1px solid rgba(226, 232, 240, 0.6);\n    box-shadow: 0 20px 50px rgba(15, 23, 42, 0.12), 0 8px 24px rgba(139, 35, 72, 0.08), inset 0 1px 0 rgba(255, 255, 255, 0.5);\n    backdrop-filter: blur(28px) saturate(1.4);\n    -webkit-backdrop-filter: blur(28px) saturate(1.4);\n    cursor: pointer;\n    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n.chart-card:hover {\n    transform: translateY(-2px);\n    box-shadow: 0 24px 60px rgba(15, 23, 42, 0.16), 0 12px 32px rgba(139, 35, 72, 0.12), inset 0 1px 0 rgba(255, 255, 255, 0.5);\n    border-color: var(--theme-accent-color);\n}\n\n.calendar-card .calendar-grid {\n    display: grid;\n    grid-template-columns: repeat(5, 1fr);\n    gap: 8px;\n    font-size: 13px;\n}\n\n.calendar-card .cal-day {\n    padding: 10px;\n    border-radius: 12px;\n    background: rgba(255, 255, 255, 0.12);\n    text-align: center;\n    font-weight: 700;\n    color: var(--theme-text-primary);\n    border: 1px solid rgba(255, 255, 255, 0.12);\n}\n\n.calendar-card .cal-day.muted {\n    opacity: 0.72;\n}\n\n.calendar-card .cal-slot {\n    grid-column: span 5;\n    padding: 10px 12px;\n    border-radius: 12px;\n    background: rgba(255, 255, 255, 0.08);\n    border: 1px solid rgba(255, 255, 255, 0.12);\n    color: var(--theme-text-primary);\n}\n\n.calendar-card .cal-slot.accent {\n    background: rgba(118, 75, 162, 0.12);\n    border-color: rgba(118, 75, 162, 0.24);\n}\n\n.chart-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 10px;\n}\n\n.chart-title {\n    font-size: 15px;\n    font-weight: 700;\n    color: var(--theme-text-primary);\n    margin: 0;\n    display: inline-flex;\n    align-items: center;\n    gap: 6px;\n}\n\n.chart-legend {\n    display: flex;\n    align-items: center;\n    gap: 10px;\n}\n\n.chart-legend-item {\n    display: inline-flex;\n    align-items: center;\n    gap: 6px;\n    font-size: 14px;\n    color: var(--theme-text-secondary);\n}\n\n.chart-legend-dot {\n    width: 10px;\n    height: 10px;\n    border-radius: 50%;\n    background: var(--theme-accent-color);\n    box-shadow: 0 0 0 4px rgba(155, 89, 182, 0.12);\n}\n\n.chart-body {\n    position: relative;\n    margin-top: 6px;\n    min-height: 140px;\n    background: linear-gradient(180deg, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.28));\n    border-radius: 12px;\n    border: 1px solid rgba(255, 255, 255, 0.2);\n    overflow: hidden;\n}\n\n.sparkline-placeholder {\n    position: absolute;\n    inset: 0;\n    background: linear-gradient(135deg, rgba(139, 35, 72, 0.14), rgba(155, 89, 182, 0.12));\n    opacity: 0.55;\n}\n\n.chart-metrics {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));\n    gap: 10px;\n    margin-top: 12px;\n}\n\n.chart-metric {\n    background: rgba(255, 255, 255, 0.82);\n    border: 1px solid rgba(255, 255, 255, 0.22);\n    border-radius: 10px;\n    padding: 10px 12px;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    gap: 10px;\n    box-shadow: 0 8px 22px rgba(139, 35, 72, 0.10);\n}\n\n.chart-metric .label {\n    font-size: 14px;\n    color: var(--theme-text-secondary);\n}\n\n.chart-metric .value {\n    font-size: 16px;\n    font-weight: 700;\n    color: var(--theme-text-primary);\n}\n\n.list-card .mini-table {\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n}\n\n.mini-table-row {\n    display: grid;\n    grid-template-columns: 1.2fr 0.9fr 0.7fr 0.7fr 0.9fr;\n    align-items: center;\n    gap: 10px;\n    padding: 12px 10px;\n    border-radius: 12px;\n    background: rgba(255, 255, 255, 0.76);\n    border: 1px solid rgba(0, 0, 0, 0.04);\n    box-shadow: 0 10px 26px rgba(0, 0, 0, 0.06);\n}\n\n.mini-table-head {\n    background: rgba(255, 255, 255, 0.54);\n    border: 1px solid rgba(0, 0, 0, 0.04);\n    font-weight: 700;\n    color: var(--theme-text-secondary);\n    text-transform: uppercase;\n    font-size: 12px;\n    letter-spacing: 0.08em;\n}\n\n.mini-table-row span {\n    color: var(--theme-text-primary);\n    font-size: 14px;\n}\n\n.mini-table-row .cell-strong {\n    font-weight: 700;\n}\n\n.chip {\n    display: inline-flex;\n    align-items: center;\n    padding: 4px 10px;\n    border-radius: 999px;\n    font-weight: 700;\n    font-size: 12px;\n    border: 1px solid transparent;\n}\n\n.chip-low {\n    background: rgba(56, 239, 125, 0.14);\n    color: #1c7d4f;\n    border-color: rgba(56, 239, 125, 0.28);\n}\n\n.chip-medium {\n    background: rgba(248, 197, 71, 0.16);\n    color: #8a5b00;\n    border-color: rgba(248, 197, 71, 0.28);\n}\n\n.chip-high {\n    background: rgba(245, 87, 108, 0.16);\n    color: #982b3a;\n    border-color: rgba(245, 87, 108, 0.3);\n}\n\n.contact-chips {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 8px;\n}\n\n.contact-chip {\n    border: 1px solid rgba(0, 0, 0, 0.06);\n    background: linear-gradient(145deg, rgba(118, 75, 162, 0.08), rgba(102, 126, 234, 0.08));\n    color: var(--theme-text-primary);\n    padding: 8px 12px;\n    border-radius: 999px;\n    font-weight: 700;\n    font-size: 13px;\n    cursor: pointer;\n    transition: all 0.2s ease;\n    box-shadow: 0 10px 26px rgba(0, 0, 0, 0.06);\n}\n\n.contact-chip:hover,\n.contact-chip:focus-visible {\n    outline: none;\n    border-color: var(--theme-accent-color);\n    box-shadow: 0 12px 30px rgba(118, 75, 162, 0.16);\n    transform: translateY(-1px);\n}\n\n.contact-chip:active {\n    transform: translateY(0);\n}\n\n.status-dot {\n    display: inline-flex;\n    align-items: center;\n    gap: 6px;\n    font-weight: 600;\n}\n\n.status-dot::before {\n    content: '';\n    width: 10px;\n    height: 10px;\n    border-radius: 50%;\n    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.06);\n}\n\n.status-dot.ok::before {\n    background: #38ef7d;\n    box-shadow: 0 0 0 4px rgba(56, 239, 125, 0.14);\n}\n\n.status-dot.warn::before {\n    background: #f8c547;\n    box-shadow: 0 0 0 4px rgba(248, 197, 71, 0.18);\n}\n\n.status-dot.risk::before {\n    background: #f5576c;\n    box-shadow: 0 0 0 4px rgba(245, 87, 108, 0.16);\n}\n\n.icon-animated {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    width: 28px;\n    height: 28px;\n    margin-right: 4px;\n    color: var(--theme-primary-color);\n}\n\n.icon-animated svg {\n    stroke-linecap: round;\n    stroke-linejoin: round;\n    overflow: visible;\n}\n\n.icon-animated svg * {\n    stroke-dasharray: 120;\n    stroke-dashoffset: 120;\n    animation: line-draw 1.2s ease forwards;\n    animation-delay: 0.05s;\n}\n\n@keyframes line-draw {\n    to {\n        stroke-dashoffset: 0;\n    }\n}\n\n.dashboard-card {\n    background: linear-gradient(160deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.78));\n    border: 1px solid rgba(255, 255, 255, 0.6);\n    border-radius: 16px;\n    padding: var(--dashboard-card-padding);\n    box-shadow: 0 14px 34px rgba(31, 38, 135, 0.12), var(--shadow-sm);\n    backdrop-filter: blur(12px) saturate(1.08);\n    -webkit-backdrop-filter: blur(12px) saturate(1.08);\n    margin-bottom: 12px;\n    color: var(--theme-text-primary);\n    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.2s ease;\n}\n\n.dashboard-card.scrolled-collapsed {\n    transform: scale(0.98);\n    opacity: 0.85;\n}\n\n/* Obsoleto - tabs antiguos ocultos usan .dashboard-card */\n/* .agent-dashboard .dashboard-card {\n    padding: 18px 16px;\n    border-radius: 14px;\n}\n\n.agent-dashboard .dashboard-stats {\n    gap: 12px;\n    margin-bottom: 12px;\n} */\n\n/* Simple card title when no .card-header is used (sidebar cards) */\n.dashboard-card>h3 {\n    font-family: 'Cinzel', serif;\n    font-size: 1.25rem;\n    color: var(--theme-text-primary);\n    margin: 0 0 16px 0;\n}\n\n.card-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    gap: 12px;\n    margin-bottom: var(--dashboard-card-header-margin);\n    padding-bottom: var(--dashboard-card-header-padding);\n    border-bottom: 2px solid var(--theme-surface-border);\n}\n\n.card-header h3 {\n    font-family: 'Cinzel', serif;\n    font-size: 1.25rem;\n    color: var(--theme-text-primary);\n    margin: 0;\n}\n\n/* Lists */\n.policies-list,\n.quotes-list,\n.documents-list,\n.tasks-list,\n.alert-list,\n.activity-list,\n.prelaunch-checklist {\n    display: flex;\n    flex-direction: column;\n    gap: 14px;\n}\n\n.task-item,\n.alert-item,\n.activity-item {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    gap: 12px;\n    padding: 14px 16px;\n    background: var(--acrylic-bg-light);\n    border: 1px solid var(--theme-accent-border-subtle);\n    border-radius: 12px;\n    color: var(--theme-text-primary);\n}\n\n.task-title,\n.alert-title,\n.activity-title {\n    margin: 0 0 4px 0;\n    font-weight: 700;\n}\n\n.task-meta,\n.alert-meta,\n.activity-meta {\n    margin: 0;\n    color: var(--theme-text-secondary);\n    font-size: 0