der-color: var(--theme-accent-border);\n    transform: translateX(3px);\n    box-shadow: var(--shadow);\n}\n\n.policy-icon {\n    font-size: 2rem;\n    width: 52px;\n    height: 52px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background: var(--acrylic-bg-light);\n    border: 1px solid var(--theme-surface-border);\n    border-radius: 12px;\n    flex-shrink: 0;\n}\n\n.policy-info,\n.quote-info {\n    flex: 1;\n    min-width: 0;\n}\n\n.policy-actions {\n    display: flex;\n    align-items: center;\n    gap: 10px;\n    flex-shrink: 0;\n}\n\n.policy-info h4,\n.quote-info h4 {\n    font-size: 1.05rem;\n    color: var(--theme-text-primary);\n    margin: 0 0 6px 0;\n}\n\n.policy-info p,\n.quote-info p {\n    color: var(--theme-text-secondary);\n    font-size: 0.92rem;\n    margin: 0 0 10px 0;\n}\n\n.policy-meta {\n    display: flex;\n    align-items: center;\n    flex-wrap: wrap;\n    gap: 10px;\n    font-size: 0.85rem;\n    color: var(--theme-text-secondary);\n}\n\n.quote-date {\n    font-size: 0.85rem;\n    color: var(--theme-text-secondary);\n}\n\n.recent-docs-list {\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n}\n\n.doc-item {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    gap: 12px;\n    padding: 12px 14px;\n    background: rgba(255, 255, 255, 0.82);\n    border: 1px solid rgba(0, 0, 0, 0.06);\n    border-radius: 12px;\n    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.08);\n}\n\n.doc-meta {\n    display: flex;\n    flex-direction: column;\n    gap: 4px;\n}\n\n.doc-meta strong {\n    font-size: 14px;\n    color: var(--theme-text-primary);\n}\n\n.doc-meta span {\n    color: var(--theme-text-secondary);\n    font-size: 12px;\n}\n\n.doc-actions {\n    display: inline-flex;\n    align-items: center;\n    gap: 8px;\n}\n\n.contact-modal {\n    position: fixed;\n    inset: 0;\n    display: none;\n    align-items: center;\n    justify-content: center;\n    z-index: 200;\n}\n\n.contac